    </template>
    <script>
    import * as Xlsx from 'xlsx'
        export default{
            computed:{
                maxPage() {  //   (and   )
                    return this.paginate_total
                },
                startPage() { //   
                    return (Math.trunc((this.current - 1) / this.pageCount) * this.pageCount) + 1
                },
                endPage() { //   
                    let end = this.startPage + this.pageCount - 1
                    return end < this.maxPage ? end : this.maxPage
                },
                paginate_total_unit(){
                    let units = [];
                    for(let num = this.startPage;num <=this.endPage;num++){
                        units.push(num);
                    }
                    return units;
                }
            },
            data(){
                return{
                    get_cct : '',
                    get_cpt : '',
                    sea_date_start: '',
                    sea_date_end: '',
                    sea_use: '',
                    sea_cct: '',
                    sea_cpt: '',
                    sea_phonenum: '',
                    sea_carnum: '',
                    get_couponsum: '',
                    get_couponresult: '',
                    paginate : 25,
                    sea_id : '',
                    paginate_total: 0,
                    current: 1,
                    pageCount : 10, //    
                    get_memdetail : '',
                    date_type : "reg",
                    index_no : 0,
                    coupon_number : ''
                }
            },
            created(){
                this.get_select();
                //this.set_yes();
                this.set_today();
                this.get_search();
            },
            methods : {
                get_search(){
                    if(this.sea_date_start > this.sea_date_end){
                        alert("  .");
                        return false;
                    }
                    this.current = 1;
                    this.index_no = 0;
                    this.get_couponsum = '';
                    this.get_couponresult = '';
                    this.$http.post(this.$server+'/admin/getCouponSum',
                    {
                        start_date : this.sea_date_start,
                        end_date : this.sea_date_end,
                        date_type : this.date_type,
                        coupon_type : this.sea_cct,
                        is_use : this.sea_use,
                        publish_type : this.sea_cpt,
                        car_no : this.sea_carnum,
                    }
                    ,{headers : {
                        auth_key :'c83b4631-ff58-43b9-8646-024b12193202'
                    }
                    }).then((res) => {
                        this.get_couponsum = res.data;
                    });
                    this.$http.post(this.$server+'/admin/getCouponList',
                    {
                        start_date : this.sea_date_start,
                        end_date : this.sea_date_end,
                        date_type : this.date_type,
                        coupon_type : this.sea_cct,
                        is_use : this.sea_use,
                        publish_type : this.sea_cpt,
                        car_no : this.sea_carnum,
                        view_count : 25,
                        start_count : (this.current-1) * 25,
                    }
                    ,{headers : {
                        auth_key :'c83b4631-ff58-43b9-8646-024b12193202'
                        }
                    }).then((res) => {
                        this.get_couponresult = res.data
                        console.log(res.data);
                        this.paginate_total = Math.ceil(this.get_couponsum.account_coupon/this.paginate)
                    })
    
                },
                return_one(on_num){
                    if(on_num != undefined){
                        const parts = on_num.toString().split('.');
                        parts[0] = parts[0].replace(/\B(?=(\d{3})+(?!\d))/g, ',');
                        return parts.join('.');
                    }  
                },
                get_select(){
                    this.$http.post(this.$server+'/admin/getCodeList',
                    {
                        code_type : 'CCT'
                    }
                    ,{headers : {
                        auth_key :'c83b4631-ff58-43b9-8646-024b12193202'
                        }
                    }).then((res) => {
                        
                        this.get_cct = res.data
    
    
                    })
                    this.$http.post(this.$server+'/admin/getCodeList',
                    {
                        code_type : 'CPT'
                    }
                    ,{headers : {
                        auth_key :'c83b4631-ff58-43b9-8646-024b12193202'
                        }
                    }).then((res) => {
                        
                        this.get_cpt = res.data
                    })
                },
                setPaginate: function (i) {
                    if (this.current == 1) {
                        return i < this.paginate;
                    }
                    else {
                        return (i >= (this.paginate * (this.current - 1)) && i < (this.current * this.paginate));
                    }
                },
                updateCurrent(i) {
                    this.current = i;
                    this.index_no = (this.current-1)*25;
                    this.$http.post(this.$server+'/admin/getCouponList',
                    {
                        start_date : this.sea_date_start,
                        end_date : this.sea_date_end,
                        date_type : this.date_type,
                        coupon_type : this.sea_cct,
                        is_use : this.sea_use,
                        publish_type : this.sea_cpt,
                        car_no : this.sea_carnum,
                        view_count : 25,
                        start_count : (this.current-1) * 25,
                    }
                    ,{headers : {
                        auth_key :'c83b4631-ff58-43b9-8646-024b12193202'
                        }
                    }).then((res) => {
                        console.log(res.data);
                        this.get_couponresult = res.data
                    })
                },
                register_qrcoupon(){
                      this.$http.post(this.$server+'/admin/setCouponUse',
                    {
                        coupon_code : this.coupon_number,
                        coupon_type : "CCT002",
                    }
                    ,{headers : {
                        auth_key :'c83b4631-ff58-43b9-8646-024b12193202'
                        }
                    }).then((res) => {
                       if(res.data.result_code == "Y"){
                        alert("  ")
                        location.reload();
                       }else{
                        alert(" QR .")
                       }
                    })
                },
                set_yes: function(){
                    const d = new Date();
                    const b = new Date();
                    const year = d.getFullYear(); // 
                    const month = (d.getMonth()+1);   // 
                    const day = d.getDate();
                    var pastDate = b.getDate() - 1;
                    b.setDate(pastDate);
                    const b_year = b.getFullYear(); // 
                    const b_month = (b.getMonth()+1);   // 
                    const b_day = b.getDate();
                    this.sea_date_start = b_year+'-'+b_month.toString().padStart(2,'0')+'-'+b_day.toString().padStart(2,'0')
                    this.sea_date_end = year+'-'+month.toString().padStart(2,'0')+'-'+day.toString().padStart(2,'0')
                },
                set_today: function(){
                    const d = new Date();
                    const year = d.getFullYear(); // 
                    const month = (d.getMonth()+1);   // 
                    const day = d.getDate();
                    this.sea_date_start = year+'-'+month.toString().padStart(2,'0')+'-'+day.toString().padStart(2,'0')
                    this.sea_date_end = year+'-'+month.toString().padStart(2,'0')+'-'+day.toString().padStart(2,'0')
                },
                set_weak: function(){
                    const d = new Date();
                    const b = new Date();
                    const year = d.getFullYear(); // 
                    const month = (d.getMonth()+1);   // 
                    const day = d.getDate();
                    var pastDate = b.getDate() - 7;
                    b.setDate(pastDate);
                    const b_year = b.getFullYear(); // 
                    const b_month = (b.getMonth()+1);   // 
                    const b_day = b.getDate();
                    this.sea_date_start = b_year+'-'+b_month.toString().padStart(2,'0')+'-'+b_day.toString().padStart(2,'0')
                    this.sea_date_end = year+'-'+month.toString().padStart(2,'0')+'-'+day.toString().padStart(2,'0')
                },
                set_month: function(){
                    const d = new Date();
                    const year = d.getFullYear(); // 
                    const month = (d.getMonth()+1);   // 
                    const day = d.getDate();
                    this.sea_date_start = year+'-'+(month-1).toString().padStart(2,'0')+'-'+day.toString().padStart(2,'0')
                    this.sea_date_end = year+'-'+month.toString().padStart(2,'0')+'-'+day.toString().padStart(2,'0')
                },
                set_year: function(){
                    const d = new Date();
                    const year = d.getFullYear(); // 
                    const month = (d.getMonth()+1);   // 
                    const day = d.getDate();
                    this.sea_date_start = year+'-'+month.toString().padStart(2,'0')+'-'+day.toString().padStart(2,'0')
                    this.sea_date_end = year+'-'+(month-1).toString().padStart(2,'0')+'-'+day.toString().padStart(2,'0')
                },
                return_date(date){
                    var today = new Date(date);
                    today.setHours(today.getHours() + 9);
                    return today.toISOString().replace('T', ' ').substring(0, 19);
                },
                makeExcelFile5 () {
                    const workBook = Xlsx.utils.book_new()
                    const workSheet = Xlsx.utils.json_to_sheet(this.get_couponresult)
                    Xlsx.utils.book_append_sheet(workBook, workSheet, '')
                    Xlsx.writeFile(workBook, 'output.xlsx')
                },
            }
        }
    
    </script>