                    }
                });
            },
            gate_down(gate_no){
                this.$http.post(this.$server+'/admin/closeGate',
                {
                    gate_no : gate_no
                }
                ,{headers : {
                    auth_key :'c83b4631-ff58-43b9-8646-024b12193202'
                }
